.home {
    display: flex;
    position: relative;
    height: 500px;
    width: 100%;
    align-items: center;
    align-self: center;
    align-content: center;
    justify-content: center;
    background-color:#ECD8CF;
    text-align: center;
}

a {
    text-decoration: none;
}

.bgimage {
    display: flex;
    position: relative;
    height: 100%;
    width: 100%;
    border-radius: 0px 0px 5% 5%;
}

.HomeText {
    display: grid;
    font-family: monospace;
    position: absolute;
    z-index: 1;
    font-size: xx-large;
    transform: translate(-110%, -10%);
    text-align: left;
    justify-content: center;
}

.title {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: #1C5C4D;
    font-size: 60px;
}

.description {
    font-family: monospace;
    color: aliceblue;
    font-size: large;
    font-size: xx-large;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: #1C5C4D;
    background-color: #ECD8CF;
    height: 50px;
    width: 250px;
    border: 5px solid #1C5C4D;
    margin: 50px 0px 0px 0px;
    border-radius: 20px;
    padding: 40px 30px 40px 30px;
    font-size: x-large;
    box-shadow:  0 0 5px 5px rgba(0,0,0,0.2);
}

.button:hover {
    border: 5px solid #ECD8CF;
    color: #ECD8CF;
    background-color: #1C5C4D;
    transition: all 0.5s ease;
    box-shadow:  0 0 10px 10px rgba(0,0,0,0.2);
}

.ExtraDetes {
    color: #ECD8CF;
    display: grid;
    font-family: monospace;
    position: absolute;
    box-shadow: inset 0 0 0 100px rgba(0,0,0,0.2);
    z-index: 1;
    font-size: x-large;
    transform: translate(180%, -10%);
    text-align: left;
    justify-content: center;
    border: 5px solid #ECD8CF;
    padding: 20px;
    border-radius: 20px;

}

@media screen and (max-width:1500px) {

    .HomeText {
        display: flex;
        flex-direction: column;
        font-family: monospace;
        position: absolute;
        z-index: 1;
        font-size: xx-large;
        transform: translate(0%, -30%);
        text-align: center;
        justify-content: center;
    }

    .title {
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        color: #1C5C4D;
        font-size: 60px;
    }

    .button {
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: center;
        align-content: center;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        color: #1C5C4D;
        background-color: #ECD8CF;
        height: 50px;
        width: 250px;
        border: 5px solid #1C5C4D;
        margin: 50px 0px 0px 19%;
        border-radius: 20px;
        font-size: x-large;
        box-shadow:  0 0 5px 5px rgba(0,0,0,0.2);
    }

    .ExtraDetes {
        color: #ECD8CF;
        display: grid;
        font-family: monospace;
        position: absolute;
        box-shadow: inset 0 0 0 100px rgba(0,0,0,0.2);
        z-index: 1;
        font-size: x-large;
        transform: translate(0%, 100%);
        text-align: center;
        justify-content: center;
        border: 5px solid #ECD8CF;
        padding: 20px;
        border-radius: 20px;
    }



}